'use client';

import { useEffect, useState } from 'react';
import { useSearchParams, useRouter } from 'next/navigation';
import axios from '@/lib/axios';
import { 
    CheckCircle2, Calendar, Clock, MapPin, User, ArrowRight, Printer, Download 
} from 'lucide-react';
import QRCode from "react-qr-code";
import { Button } from '@/components/ui/button';
import { Card, CardContent } from '@/components/ui/card';
import { Separator } from '@/components/ui/separator';
import { Badge } from '@/components/ui/badge';
import { Booking } from '@/types';
import Link from 'next/link';

export default function BookingSuccessPage() {
    const router = useRouter();
    const searchParams = useSearchParams();
    const bookingId = searchParams.get('id');

    const [booking, setBooking] = useState<Booking | null>(null);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        if (!bookingId) return;

        const fetchBooking = async () => {
            try {
                const res = await axios.get(`/api/bookings/${bookingId}`);
                setBooking(res.data);
            } catch (error) {
                console.error("Gagal memuat booking", error);
            } finally {
                setLoading(false);
            }
        };

        fetchBooking();
    }, [bookingId]);

    const formatRupiah = (num: number) => 
        new Intl.NumberFormat('id-ID', { style: 'currency', currency: 'IDR', minimumFractionDigits: 0 }).format(num);
    
    const formatDate = (dateString: string) => 
        new Date(dateString).toLocaleDateString('id-ID', { weekday: 'long', day: 'numeric', month: 'long', year: 'numeric' });

    if (!bookingId || loading) {
        return (
            <div className="min-h-screen bg-[#f8f9fa] flex items-center justify-center">
                <div className="animate-pulse flex flex-col items-center">
                    <div className="h-12 w-12 bg-gray-200 rounded-full mb-4"></div>
                    <div className="h-4 w-48 bg-gray-200 rounded"></div>
                </div>
            </div>
        );
    }

    if (!booking) return null;

    return (
        <div className="min-h-screen bg-[#f8f9fa] py-12 px-4 flex justify-center items-start">
            <div className="max-w-lg w-full bg-white rounded-[2rem] shadow-2xl overflow-hidden relative border border-gray-100 print:shadow-none print:border-none">
                
                {/* Header Decoration */}
                <div className="bg-[#D93F21] h-32 relative print:hidden">
                    <div className="absolute inset-0 bg-[url('/images/pattern.png')] opacity-10"></div>
                    <div className="absolute -bottom-10 left-0 right-0 flex justify-center">
                        <div className="w-20 h-20 bg-white rounded-full flex items-center justify-center p-1 shadow-lg">
                            <div className="w-full h-full bg-green-100 rounded-full flex items-center justify-center text-green-600">
                                <CheckCircle2 className="w-10 h-10" />
                            </div>
                        </div>
                    </div>
                </div>

                <div className="pt-14 pb-8 px-6 md:px-8 text-center print:pt-4">
                    <h1 className="text-2xl font-extrabold text-gray-900 mb-1">Booking Berhasil!</h1>
                    <p className="text-gray-500 text-sm mb-6">
                        Terima kasih, pesanan Anda sedang diverifikasi admin.
                    </p>

                    {/* TICKET CARD */}
                    <div className="bg-gray-50 border border-gray-200 rounded-2xl p-6 relative text-left">
                        {/* Cutout Effect */}
                        <div className="absolute top-1/2 -left-3 w-6 h-6 bg-white rounded-full border-r border-gray-200"></div>
                        <div className="absolute top-1/2 -right-3 w-6 h-6 bg-white rounded-full border-l border-gray-200"></div>

                        <div className="flex justify-between items-start mb-4">
                            <div>
                                <p className="text-xs text-gray-400 font-bold uppercase tracking-wider">Kode Booking</p>
                                <p className="text-xl font-mono font-black text-gray-900 tracking-wide">{booking.kode_booking}</p>
                            </div>
                            <div className="bg-white p-1 rounded-lg border border-gray-100">
                                <QRCode value={booking.kode_booking} size={50} />
                            </div>
                        </div>

                        <Separator className="mb-4 border-dashed border-gray-300" />

                        <div className="space-y-4">
                            <div className="flex items-start gap-3">
                                <MapPin className="w-5 h-5 text-[#D93F21] mt-0.5" />
                                <div>
                                    <p className="text-xs text-gray-500 font-bold uppercase">Lapangan</p>
                                    <p className="font-bold text-gray-800">{booking.lapangan?.nama_lapangan}</p>
                                </div>
                            </div>

                            <div className="grid grid-cols-2 gap-4">
                                <div className="flex items-start gap-3">
                                    <Calendar className="w-5 h-5 text-blue-500 mt-0.5" />
                                    <div>
                                        <p className="text-xs text-gray-500 font-bold uppercase">Tanggal</p>
                                        <p className="text-sm font-medium text-gray-800">{formatDate(booking.tanggal_booking)}</p>
                                    </div>
                                </div>
                                <div className="flex items-start gap-3">
                                    <Clock className="w-5 h-5 text-orange-500 mt-0.5" />
                                    <div>
                                        <p className="text-xs text-gray-500 font-bold uppercase">Jam Main</p>
                                        <p className="text-sm font-medium text-gray-800">{booking.jam_mulai.substring(0,5)} - {booking.jam_selesai.substring(0,5)}</p>
                                    </div>
                                </div>
                            </div>

                            {booking.acara && (
                                <div className="flex items-start gap-3">
                                    <User className="w-5 h-5 text-purple-500 mt-0.5" />
                                    <div>
                                        <p className="text-xs text-gray-500 font-bold uppercase">Kegiatan/Club</p>
                                        <p className="text-sm font-medium text-gray-800">{booking.acara}</p>
                                    </div>
                                </div>
                            )}
                        </div>

                        <Separator className="my-4 border-dashed border-gray-300" />

                        <div className="flex justify-between items-center">
                            <span className="text-sm text-gray-500">Status</span>
                            {booking.status_booking_id === 1 && <Badge variant="outline" className="bg-yellow-50 text-yellow-700 border-yellow-200">Menunggu Bayar</Badge>}
                            {booking.status_booking_id === 2 && <Badge variant="outline" className="bg-blue-50 text-blue-700 border-blue-200">Verifikasi Admin</Badge>}
                            {booking.status_booking_id === 3 && <Badge className="bg-green-600 hover:bg-green-700">Terkonfirmasi</Badge>}
                        </div>
                    </div>
                </div>

                {/* Footer Actions */}
                <div className="bg-gray-50 p-6 border-t border-gray-100 flex flex-col gap-3 print:hidden">
                    <Button onClick={() => window.print()} variant="outline" className="w-full border-gray-300 text-gray-700">
                        <Printer className="w-4 h-4 mr-2" /> Cetak Tiket
                    </Button>
                    <Link href="/pelanggan/dashboard" className="w-full">
                        <Button className="w-full bg-[#D93F21] hover:bg-[#b9351b] font-bold text-lg h-12 rounded-xl shadow-lg shadow-orange-100">
                            Kembali ke Dashboard
                        </Button>
                    </Link>
                </div>
            </div>
            
            <style jsx global>{`
                @media print {
                    body * { visibility: hidden; }
                    .max-w-lg, .max-w-lg * { visibility: visible; }
                    .max-w-lg { position: absolute; top: 0; left: 0; width: 100%; box-shadow: none; border: none; }
                    .print\\:hidden { display: none !important; }
                }
            `}</style>
        </div>
    );
}